<template>
  <!-- 功能详情页 -->
  <mrc_ess_skLoaders :skLoading='skLoading'>
    <div>
      <mrc_ess_iconGroup :is_disabled="disabled"
                         :showEdit="hasBtn('edit-module')"
                         :show_del="hasBtn('delete-module')"
                         @handleReturn='handleReturn'
                         @handleEdit='handleEdit'
                         @handleDel='handleDel'></mrc_ess_iconGroup>
      <v-card>
        <v-card-subtitle style="text-align:right">
          {{uuid}}
        </v-card-subtitle>
        <v-card-text>
          <v-form ref="func_detail">
            <v-row>
              <!-- <v-col cols="12"
                     sm="6">
                <v-text-field :label="$t('label_The_name_of_the_module')+'*'"
                              required
                              :readonly="disabled"
                              v-model="detailData.module_name"
                              append-icon="mdi-longitude"
                              :rules="$text_rules($t('label_The_name_of_the_module'))">
                </v-text-field>
                
              </v-col>
              <v-col cols="12"
                     sm="6">
                <v-text-field :label="$t('label_short_code_of_module')+'*'"
                              required
                              :readonly="disabled"
                              v-model="detailData.module_shortcode"
                              append-icon="mdi-longitude"
                              :rules="$text_rules($t('label_short_code_of_module'))">
                </v-text-field>
              </v-col>
              <v-col cols="12"
                     sm="6">
                <v-text-field :label="$t('label_Module_description')+'*'"
                              required
                              :readonly="disabled"
                              v-model="detailData.module_description"
                              append-icon="mdi-longitude"
                              :rules="$text_rules($t('label_Module_description'))">
                </v-text-field>
              </v-col> -->

              <v-col cols="12"
                     sm="6">
                <mrc_ess_multiText :label="$t('label_The_name_of_the_module')+'*'"
                                   :isNational="judegNational('module_name')"
                                   :readonly="disabled"
                                   v-model="detailData.module_name"
                                   append-icon="mdi-longitude"
                                   :rules="$text_rules($t('label_The_name_of_the_module'))">
                </mrc_ess_multiText>

              </v-col>
              <v-col cols="12"
                     sm="6">
                <mrc_ess_multiText :label="$t('label_short_code_of_module')+'*'"
                                   :isNational="judegNational('module_shortcode')"
                                   :readonly="disabled"
                                   v-model="detailData.module_shortcode"
                                   append-icon="mdi-longitude"
                                   :rules="$text_rules($t('label_short_code_of_module'))">
                </mrc_ess_multiText>
              </v-col>
              <v-col cols="12"
                     sm="6">
                <mrc_ess_multiText :label="$t('label_Module_description')+'*'"
                                   :isNational="judegNational('module_description')"
                                   :readonly="disabled"
                                   v-model="detailData.module_description"
                                   append-icon="mdi-longitude"
                                   :rules="$text_rules($t('label_Module_description'))">
                </mrc_ess_multiText>
              </v-col>

              <v-col cols="12"
                     sm="6">
                <v-text-field :label="$t('label_version')+'*'"
                              :placeholder="$t('message_Automatically_generated_after_adding')"
                              readonly
                              v-model="detailData.version"
                              chips></v-text-field>
              </v-col>
              <v-col cols="12"
                     sm="6">
                <!-- <v-select v-model="detailData.client_type"
                          :items="client_type"
                          :readonly="disabled"
                          item-value="dict_value"
                          item-text="dict_label"
                          :label="$t('label.client_type') + '*'"
                          required
                          chips
                          multiple
                          :rules="[v => v.length>0 || $t('label.client_type')]"></v-select> -->
                <mrc_ess_dictName v-model="detailData.client_type"
                                  :disabled='disabled'
                                  :label="$t('label_Client_type') + '*'"
                                  :rules="false"
                                  :errMsg="$errors('detailData','client_type',requiredRule)"
                                  multiple
                                  dict_name='client_type'
                                  chips></mrc_ess_dictName>
              </v-col>
              <v-col cols="12"
                     sm="6">
                <!-- <v-select :items="top_module"
                          :readonly="disabled"
                          label="上级模块"
                          v-model="detailData.tricode_parent"
                          item-text='view_fields'
                          item-value='value_field'
                          chips></v-select> -->

                <mrc_ess_prompt :select_arr="top_module"
                                :disabled="disabled"
                                :rules='false'
                                :label="$t('label_The_superior_module')+'*'"
                                v-model="detailData.tricode_parent"
                                item-text='view_fields'
                                item-value='value_field'
                                chips></mrc_ess_prompt>
                <!-- :rules="[v => v.length > 0 || $t('label.client_type')]" -->
              </v-col>

            </v-row>
          </v-form>
        </v-card-text>
      </v-card>
      <mrc_ess_submitBtn v-show="!disabled"
                         @cancel='cancel'
                         :loading='submitloading'
                         @success='success'></mrc_ess_submitBtn>
      <!-- 删除确认框 -->
      <confirm :isOpen='isOpen'
               @onCancel='onCancel'
               :showText='showText'
               @onConfirm='onConfirm'
               :isLoading='isLoading' />
    </div>
  </mrc_ess_skLoaders>
</template>

<script>
import { getBusinessList, get_detail, get_update, get_add, get_del } from '@/service/api/func_module'
import { getBusinessList as getPrompt } from '@/service/api/prompt'
import { getDataByName } from '@/service/api/data_dictionary'
// import multiText from '@/components/multi_text'
import confirm from '@/components/confirm'
// import myBtns from '@/components/c_s_btns'
// import iconGroup from '@/components/iconGroup'
import { required } from 'vuelidate/lib/validators'
export default {
  components: { confirm },
  props: {},
  data () {
    return {
      submitloading: false,
      skLoading: true,
      is_del: false,
      isLoading: false,
      showText: "",
      isOpen: false,
      top_module: [],
      disabled: true,
      table: 'mrbase_sys_modules',
      uuid: null,
      version_arr: [],
      detailData: {
        module_name: '',
        module_description: '',
        module_shortcode: '',
        tricode_parent: '',
        client_type: [],
        version: ''
      }
    };
  },
  validations: {
    detailData: {
      client_type: {
        required
      }
    }
  },
  created () { },
  async mounted () {

    await this.national('mrbase_sys_modules')
    this.skLoading = true
    try {

      // 如果有uuid就是修改 就请求详情
      const uuid = this.$route.params.uuid || this.$store.state.uuid
      const name = this.$store.getters.subPage_name


      if (uuid) {
        if (!this.$store.state.iconstatus) {
          this.disabled = false
        }
        this.$route.meta.title = name
        this.uuid = uuid
        const { data } = await get_detail({ uuid: this.uuid })
        this.detailData = this.$clone(data)
        this.detailData.client_type = this.detailData.client_type.split(',')


      } else {
        this.$route.meta.title = this.$t('label_New_module')
        // 没有就是新增就啥也不做
        this.disabled = false
      }
      // 请求上级模块高级参照
      const module_data = await getPrompt({ prompt_name: 'module_parent', 'uuid': this.uuid })
      if (module_data.code == 200) {
        let data = module_data.data
        data.unshift({ view_fields: 'null', value_field: '001' })
        // const index = data.findIndex(item => item.value_field == this.detailData.tricode)
        // console.log(index)
        // if (index != -1) {
        //   data.splice(index, 1)
        // }
        this.top_module = data
        // console.log(data)
      }
      this.skLoading = false

    } catch (error) {
      this.skLoading = false
    }

  },
  methods: {

    // 取消confirm 弹窗
    onCancel () {
      this.isOpen = false
    },
    // 弹窗确认
    async onConfirm () {
      // 如果是删除弹窗
      if (this.is_del) {
        this.isLoading = true
        const del = await get_del({
          '~table~': this.table,
          uuid: this.uuid
        })
        if (del.code == 200) {
          this.isLoading = false
          this.$store.commit('newsTips', this.$t('message_The_deletion_was_successful'))
          this.$router.go(-1)
        }
      } else {
        // 取消弹窗
        this.$router.go(-1)
      }

    },
    // 新增 修改取消
    cancel () {
      this.$myConfirm({ content: this.$t('message_Confirm_the_cancellation_of_the_edit') })
        .then(() => {
          this.$router.go(-1)
        }).catch(() => { })
      // if (this.uuid) {
      //   this.showText = '放弃修改'
      // } else {
      //   this.showText = '放弃新增'
      // }
      // this.isOpen = true
    },
    async success () {
      this.$v.detailData.client_type.$touch()
      const isSubmit = this.$refs.func_detail.validate()
      if (!isSubmit || this.$v.detailData.client_type.$error) {
        return
      }
      const obj = {
        // "~table~": this.table,
        module_description: this.detailData.module_description,
        module_name: this.detailData.module_name,
        module_shortcode: this.detailData.module_shortcode,
        tricode_parent: this.detailData.tricode_parent,
        uuid: this.uuid,
        version: this.detailData.version,
        client_type: this.$clone(this.detailData.client_type).join(),
        org_level_uuid: this.$store.state.userData.org_level_uuid
      }
      this.submitloading = true
      // 修改
      if (this.uuid) {
        const update = await get_update(obj)
        if (update.code == 200) {
          this.$store.commit('newsTips', this.$t('message_The_operation_was_successful'))
          this.$router.go(-1)
        } else {
          this.$store.commit('newsTips', this.$t('message_The_modification_failed'))
        }
      } else {
        // 新增
        const add = await get_add(obj)
        if (add.code == 200) {
          this.$store.commit('newsTips', this.$t('message_added_successfully'))
          this.$router.go(-1)
        } else {
          this.$store.commit('newsTips', this.$t('message_Add_failed'))
        }
      }
      this.submitloading = false
    },
    // 返回
    handleReturn () {
      this.$router.go(-1)
    },
    // 修改按钮
    handleEdit () {
      this.disabled = !this.disabled
    },
    // 删除按钮
    handleDel () {
      // this.isOpen = true
      // this.is_del = true
      // this.showText = '是否删除'
      this.$myConfirm({ content: this.$t('message_Confirm_the_deletion') })
        .then(async () => {
          const del = await get_del({
            '~table~': this.table,
            uuid: this.uuid
          })
          if (del.code == 200) {
            this.isLoading = false
            this.$store.commit('newsTips', this.$t('message_The_deletion_was_successful'))
            this.$router.go(-1)
          }
        }).catch(() => { })
    }
  },
  computed: {
  },
  watch: {},
};
</script>

